import { Button, FormControl, InputLabel, MenuItem, Select } from '@mui/material'
import { useState } from 'react'
import { useAppSelector, useAuth } from 'redux/hooks'

export default function MsalLogin() {
	const { onLoginWithMsal } = useAuth()
	const domains = useAppSelector((s) => s.UI.allowDomains)
	const [state, setState] = useState<number>(() => {
		const id = localStorage.getItem('domainId')
		return id ? parseInt(id) : domains[0].id || 1
	})
	const domain = domains.find((f) => f.id === state)
	return (
		<>
			<FormControl sx={{ m: 1, minWidth: 120 }} size="small">
				<InputLabel>Azure domain</InputLabel>
				<Select
					value={state}
					label="Azure domain"
					onChange={(e) => {
						setState(parseInt(`${e.target.value}`))
					}}
				>
					{domains.map((v) => {
						return (
							<MenuItem key={v.id} value={v.id}>
								{v.displayName}
							</MenuItem>
						)
					})}
				</Select>
			</FormControl>
			{!!domain && (
				<Button
					variant="contained"
					onClick={() => {
						sessionStorage.clear()
						localStorage.setItem('domainId', domain.id.toString())
						localStorage.setItem('clientId', domain.clientId)
						localStorage.setItem('tenantId', domain.tenantId)
						onLoginWithMsal({ clientId: domain.clientId, tenantId: domain.tenantId })
					}}
				>
					Login with 365 Account({domain.name})
				</Button>
			)}
		</>
	)
}
