import { yupResolver } from '@hookform/resolvers/yup'
import { Button, Stack, TextField } from '@mui/material'
import { SubmitHandler, useForm } from 'react-hook-form'
import { useAuth } from 'redux/hooks'
import * as yup from 'yup'

interface FormInput {
	email: string
	password: string
}
export default function WithID() {
	const { loginWithID } = useAuth()
	const schema = yup.object({
		email: yup.string().required(),
		password: yup.string().required(),
	})
	const {
		register,
		handleSubmit,
		formState: { errors },
	} = useForm<FormInput>({
		resolver: yupResolver(schema),
		defaultValues: {},
	})
	const onSubmit: SubmitHandler<FormInput> = async ({ email, password }) => {
		try {
			await loginWithID(email, password)
		} catch (error) {}
	}
	return (
		<Stack gap={1} component="form" noValidate autoComplete="off" onSubmit={handleSubmit(onSubmit)}>
			<TextField
				variant="outlined"
				required
				margin="dense"
				fullWidth
				size="small"
				label={'ID'}
				placeholder="ex:example@caa.go.jp"
				{...register('email')}
				error={'email' in errors}
			/>
			<TextField
				variant="outlined"
				required
				margin="dense"
				fullWidth
				size="small"
				label={'パスワード'}
				autoComplete="on"
				type="password"
				{...register('password')}
				error={'password' in errors}
			/>
			<Button variant="outlined" type="submit">
				Login
			</Button>
		</Stack>
	)
}
